@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700;800&display=swap');

$Poppins: 'Poppins', sans-serif;

//colors
$dark: rgb(29,29,29);
$light: #fff;
$primary: #FF8E00;
$bg:rgb(255, 255, 255);
$linecolor: #e6e6e6;

//spacing
$spacing-md: 16px;
$spacing-lg: 32px;

//border radius
$borderRadius: 12px;

//header height
$headerHeight: 100px;

@mixin breakpoint($point) {
    @if $point == 'md' {
        @media (min-width: 48em) {
            @content;
        }
    }
}
